// Colors
$orange: #ED9E3B;
$blue: #82CACB;
$pink: #E8627B;

$black: #282828;
$white: #fafafa;

$transp-white: rgba(255, 255, 255, 0.3);
$body-color: #ededed;

$transparent: rgba(0, 0, 0, 0);
$copyright-color: $white;

// Breakpoints
$bp-tablet: 770px;
$bp-phone: 500px;
$bp-xs: 415px;

// Borders
$border-radius: 3px;

// Miscellaneous
$title-glow: 0px 0px 10px $transp-white;

// Animations
//Underline fades in and slides in from the left
@keyframes underline-slide {
    0% {
        opacity: 0;
        left: -10px;
        right: 10px;
    }
    100% {
        opacity: 1;
        left: 0px;
        right: 0px;
    }
}

// Element pulses
@keyframes pulse {
    /* originally authored by Nick Pettit - https://github.com/nickpettit/glide */
    from {
      transform: scale3d(1, 1, 1);
    }
    50% {
      transform: scale3d(1.05, 1.05, 1.05);
    }
    to {
      transform: scale3d(1, 1, 1);
    }
}

// Element pulses with longer pause
@keyframes pulse-pause {
    0% {
      transform: scale3d(1, 1, 1);
    }
    70% {
      transform: scale3d(1, 1, 1);
    }
    80% {
      transform: scale3d(1.05, 1.05, 1.05);
    }
    100% {
      transform: scale3d(1, 1, 1);
    }
}

// For homepage logo animation
@keyframes draw {
    0% {
        stroke-dashoffset:0;
        stroke-dasharray:1400;
    };
    50% {
        stroke-dashoffset:1400;
        stroke-dasharray: 300
    };
    90% {
        stroke-dashoffset:50;
        stroke-dasharray: 1200
    };
}

